$(function() {
	//makeHerorDots ();
	$('.hero__slider').on('init', (e, slick)=> {
		makeCustomDots(slick, '.hero__slider', 'hero__dots', '.hero');
	});

	$('.hero__slider').slick({
		slidesToShow: 1,
		slidesToScroll: 1,
		autoplay: true,
		autoplaySpeed: 7000,
		fade: true,
		arrows: false,
		dots: false
	});

	$('.hero__slider .hero__slide').each(applyHeroBackground);

	function applyHeroBackground (idx, el) {
		let bg = $(el).css('backgroundImage');
		let target = $(el).find('.hero__tile-list');

		if(bg && target.length > 0) {
			$(target).css({
				backgroundImage: bg
			})
		}
	} 

	function makeCustomDots (slick, slider, dotsClass, dest) {
		let len = slick.$slides.length;
		if(len < 2) return;
		let ms = slick.options.autoplaySpeed

		let container = $('<div />', {
			class: `${dotsClass} timed-dots`
		});

		function overHandler() {
			container.addClass('no-animate')
		}

		function changeHandler (e, s, i) {
			let items = $(container).find('.timed-dots__item');
			items.eq(i)
				.addClass('is-active')
				.siblings().removeClass('is-active');
		} 

		function outHandler() {
			container.removeClass('no-animate')
		} 

		function handler () {
			$(slider).slick('goTo', $(this).index())
		} 

		for (var i = 0; i < len; i++) {
			let link = $('<div />', {
				class: `timed-dots__item`,
				html: `<span>0${i + 1}</span>
							<svg viewBox="0 0 56 56">
								<circle 
									cx="28" 
									cy="28" 
									r="25" 
									stroke-width="2" 
									fill="none"
									style="
										-moz-animation-duration: ${ms}ms !important;
										-webkit-animation-duration: ${ms}ms !important; 
										animation-duration: ${ms}ms !important" />
							</svg>`
			});

			if(i === 0) {
				link.addClass('is-active')
			}

			link.on('click', handler)
			container.append(link)
			$(dest).append(container);
			$(slider)
				.on('mouseover', overHandler)
				.on('mouseout', outHandler)
				.on('afterChange', changeHandler)
		}
	} 

	$('.js-select').SumoSelect({
		captionFormat: '{0} Выбрано',
		captionFormatAllSelected: '{0} Все выбраны'
	});

	// person slider
	(function() {
		$('.person-slider').each(function(idx, el) {
			let wrapper = $(el).closest('.slider-wrapper');
			let block = $(el).closest('.person-slider-wrapper');
			let list = block.find('.person-list');
		
			$(el).slick({
				fade: true,
				adaptiveHeight: true,
				appendArrows: $(wrapper),
				responsive: [
					{
						breakpoint: 1024,
						settings: {
							arrows: false,
							dots: true
						}
					},
				]
			});

			if(list.length > 0) {
				$(el).on('afterChange', function(e, slick, i){
					
					list.find('.person-list__item').eq(i)
						.addClass('is-active')
						.siblings('.person-list__item')
						.removeClass('is-active')
				})
			}
		});

		function personListHandler () {
			let slider = $(this).closest('.person-slider-wrapper').find('.person-slider');

			if($(slider).length > 0) {
				$(slider).slick('goTo', $(this).index())
			}
		}

		$('.person-list__item').on('click', personListHandler);
	})();

	// service slider
	(function() {
		let list = $('.service-slider');

		function handler (idx, el) {
			let wrapper = $(el).closest('.slider-wrapper');
			$(el).slick({
				appendArrows: $(wrapper),
				slidesToShow: 5,
				slidesToScroll: 5,
				infinite: false,
				responsive: [
					{
						breakpoint: 1200,
						settings: {
							arrows: false,
							dots: true
						}
					},
					{
						breakpoint: 1024,
						settings: {
							arrows: false,
							slidesToShow: 3,
							slidesToScroll: 3,
							dots: true
						}
					},
					{
						breakpoint: 680,
						settings: {
							arrows: false,
							slidesToShow: 2,
							slidesToScroll: 2,
							dots: true
						}
					}
				]
			});
		}

		if(list.length) {
			list.each(handler);
		}
	})();

	//mapped link
	(function(){
		function handler (e) {
			e.preventDefault();
			let target = $(this).closest('.mapper').addClass('is-active');
		} 
		$('.mapper__link').on('click', handler);
	})();

	// lined list 
	(function(){
		let list = $('.lined-list, .pillar-list');
		if(list.length) {
			$('.lined-list, .pillar-list').each(makeSlider);
		}
		function makeSlider () {
			let list = $(this);

			let listWrapper = $('<div />', {
				class: 'lined-list-wrapper slider-wrapper'
			});
			list.wrap(listWrapper);

			let wrapper = $(list).closest('.lined-list-wrapper');

			list.slick({
				appendArrows: $(wrapper),
				infinite: false,
				slidesToShow: 5,
				responsive: [
					{
						breakpoint: 1024,
						settings: {
							slidesToShow: 3,
							slidesToScroll: 1,
							arrows: false,
							dots: true
						}
					},
					{
						breakpoint: 780,
						settings: {
							slidesToShow: 2,
							slidesToScroll: 1,
							arrows: false,
							dots: true
						}
					}
				]
			})
		} 

		$('.lined-list, .pillar-list').on('setPosition', function () {
			$(this).find('.slick-slide').height('auto');
			var slickTrack = $(this).find('.slick-track');
			var slickTrackHeight = $(slickTrack).height();
			$(this).find('.slick-slide').css('height', slickTrackHeight + 'px');
		});

		// wrap tables

		$('.wysiwyg table').wrap('<div class="table-wrap"></div>');
	})();

	// carousel 
	(function () {
		let carousel = $('.carousel');
		if(carousel.length) {
			carousel.on('init', (e, slick)=> {
				makeCustomDots(slick, '.carousel', 'carousel__dots', '.carousel');
			});

			carousel.slick({
				slidesToShow: 1,
				slidesToScroll: 1,
				autoplay: true,
				autoplaySpeed: 7000,
				arrows: false,
				dots: false,
				fade: true
			});
		}
	})();

	// tabs
	(function(){

		function Tab (opts) {
			const self = this;
			const {el, wrapperClass, unitClass, btnClass, selectContainer} = opts;

			this.el = $(el);
			this.wrapper = $(el).closest(wrapperClass);
			this.select = null;
			this.selectContainer = selectContainer ? this.el.find(selectContainer) : el; 

			this.applyBindings = () => {
				$(btnClass).on('click', self.clickHandler);
				this.select.on('change',self.selectHandler);
				this.select.SumoSelect({});
			}

			this.mapItem = (idx, el) => {
				return $('<option></option>', {
					attr: {'value': $(el).attr('href')},
					text: $(el).text(),
					selected: $(el).hasClass('is-active')
				});
			}

			this.selectHandler = e => {
				const btn = $(`${btnClass}[href='${e.target.value}']`);
				const target = $(e.target.value);
				self.tabHandler(target, btn, e.target.value);
			} 

			this.makeMobileSelect = el => {
				if($(el).hasClass('is-mobile-init')) return;

				const items = $(el).find(btnClass);
				const options = items.map(self.mapItem);
				const selectWrapper = $('<div />', {class: 'form-select tabs-select'});

				this.select = $('<select></select>');
				options.each((idx, opt) => this.select.append(opt));
				selectWrapper.append(this.select)

				$(this.selectContainer).prepend(selectWrapper);
				$(el).addClass('is-mobile-init');
			}

			this.tabHandler = (target, btn, val) => {
				if(btn.hasClass('is-active') || !target.length) return;

				target
					.addClass('is-active')
					.siblings(unitClass).removeClass('is-active');
					
				btn
					.addClass('is-active')
					.siblings(btnClass).removeClass('is-active');

				self.select[0].sumo.selectItem(val)
			}

			this.clickHandler = function (e) {
				e.preventDefault();
				const s = $(this);
				const target = s.attr('href');
				self.tabHandler($(target), s, target);
			} 

			this.init = () => {
				self.makeMobileSelect(self.wrapper);
				self.applyBindings();
			}

			this.init()
			return this;
		}

		$('.newstab').each((idx, el) => {
			new Tab({
				el,
				btnClass: '.newstab-nav__item',
				unitClass: '.newstab-content__item',
				wrapperClass: '.newstab',
				selectContainer: '.newstab__grid-content'
			});
		});

		$('.tab-wrapper').each((idx, el) => {
			new Tab({
				el,
				btnClass: '.tab-nav__item',
				unitClass: '.tab',
				wrapperClass: '.tab-wrapper',
				selectContainer: '.tab-head'
			});
		});
	})();

	// invest slider
	(function(){
		// invest cards
		let card = $('.invest-card');
		let list = $('.invest-slider__list');

		function cardMap (idx, el) {
			let data = $(el).attr('data-chart');

			if(!data) return;

			try {
				data = JSON.parse(decodeURIComponent(data));
			} catch(err) {
				if(err) {
					throw new Error(err)
				}
			}

			let container = $(el).find('.invest-card__test');
			let chart =  AmCharts.makeChart(container[0], {
				"type": "serial",
		    "marginRight": 0,
		    "marginLeft": 0,
		    "autoMarginOffset": 0,
        colors: ['#fff'],
		    "dataProvider": data,
    		"dataDateFormat": "YYYY-MM-DD",
		    "valueAxes": [{
          "axisAlpha": 0,
          labelsEnabled: false,
          gridAlpha: 0,
          gridThickness: 0,
          offset: 0,
          ignoreAxisWidth: true,
          inside: true
        }],
        "graphs": [{
          "id": "g1",
          "lineThickness": 2,
          "valueField": "value",
          fillColors: ['#fff']
	      }],
		    "categoryField": "date",
		    "categoryAxis": {
		        "parseDates": true,
		        "dashLength": 1,
		        "minorGridEnabled": true,
          	labelsEnabled: false,
	          gridAlpha: 0,
	          gridThickness: 0,
	          ignoreAxisWidth: true,
	          inside: true
		    }
			});
		}

		if(!!card.length) {
			card.each(cardMap);
		}

		if(!!list.length) {
			list.slick({
				slidesToShow: 3,
				slidesToScroll: 3,
				infinite: false,
				prevArrow: $('.invest-slider__arrow--prev'),
				nextArrow: $('.invest-slider__arrow--next'),
				responsive: [
					{
						breakpoint: 1300,
						settings: {
							arrows:false
						}
					},
					{
						breakpoint: 1100,
						settings: {
							arrows:false,
							slidesToScroll: 2
						}
					},
					{
						breakpoint: 600,
						settings: {
							arrows:false,
							slidesToScroll: 1
						}
					}
				]
			});
		}
	})();

	// accordeon
	(function(){

		function accordeonHandler () {
			const self = $(this);
			const item = self.closest('.accordeon__item');
			const target = item.find('.accordeon__data');

			if(item.hasClass('is-active')) {
				target.stop(true, true).slideUp();
				item.removeClass('is-active');
			} else {
				target.stop(true, true).slideDown();
				item.addClass('is-active');
			}
		} 

		$('.accordeon__title').on('click', accordeonHandler)
	})();

	// gallery
	(function () {

		function createThumbs (e, slick) {
			const wrapper = $(slick.$slider).closest('.slider-wrapper');

			const list = $('<div />', {
				class: 'gallery-thumbs'
			});

			function itemHandler () {
				let idx = $(this).attr('data-slick') || 0;
				slick.goTo(idx);
			} 

			slick.$slides.each((idx, n) => {
				let src = $(n).attr('data-thumb') || $(n).attr('src');

				const item = $('<div />', {
					class: 'gallery-thumbs__item',
					'data-slick': idx,
					css: {
						backgroundImage: `url(${src})`
					}
				});

				if(slick.currentSlide === idx) {
					item.addClass('is-active')
				}

				item.on('click', itemHandler);
				list.append(item)
			});

			list.insertAfter(wrapper);

		} 

		$('.gallery').each((idx, el) =>{
			const wrapper = $(el).closest('.slider-wrapper');
			$(el).on('init', createThumbs);

			$(el).slick({
				slidesToShow: 1,
				slidesToScroll: 1,
				autoplay: true,
				autoplaySpeed: 7000,
				dots: false,
				appendArrows: $(wrapper),
				responsive: [
					{
						breakpoint: 1260,
						settings: {
							arrows: false
						}
					},
					{
						breakpoint: 780,
						settings: {
							dots: true,
							arrows: false
						}
					}
				]
			});

			// await nodes creation
			setTimeout(() => {
				let list = $(wrapper).siblings('.gallery-thumbs');

				$(el).on('afterChange', function(e, slick, slide) {
					let item = list.find('.gallery-thumbs__item')[slide];
					$(item).addClass('is-active')
						.siblings('.gallery-thumbs__item')
						.removeClass('is-active');
				});
				
			}, 0);
		})
	})();

	// checkboxes
	(function () {

		function applyCheckbox(el) {
			$(el).find('.checkbox__box');

			// break if already exist
			if($(el).find('.checkbox__box').length > 0) { return }

			const input = $(el).find('input');
			const t = input[0].type;
			const box = $('<div />', {
				class: `checkbox__box`
			})

			$(el).addClass('is-initialize')
			box.insertAfter(input);
		} 	

		$('.checkbox').each((idx, el) =>{
			applyCheckbox(el)
		});
	})();

	// toggle mobile block
	(function () {

		const block = $('.mobile-block');
		const body = $('body');

		function getScrollbarWidth() {
	    var outer = document.createElement("div");
	    outer.style.visibility = "hidden";
	    outer.style.width = "100px";
	    outer.style.msOverflowStyle = "scrollbar"; // needed for WinJS apps

	    document.body.appendChild(outer);

	    var widthNoScroll = outer.offsetWidth;
	    // force scrollbars
	    outer.style.overflow = "scroll";

	    // add innerdiv
	    var inner = document.createElement("div");
	    inner.style.width = "100%";
	    outer.appendChild(inner);        

	    var widthWithScroll = inner.offsetWidth;

	    // remove divs
	    outer.parentNode.removeChild(outer);

	    return widthNoScroll - widthWithScroll;
		}

		const w = getScrollbarWidth();

		function appendStyles () {
			const css = `.on-mobile-open { margin-right: ${w}px }`,
			    head = document.head || document.getElementsByTagName('head')[0];

			let style = document.getElementById('mobile-block-css');

			if(!style) {
				style = document.createElement('style');
			} else {
				return
			}

			style.type = 'text/css';
			style.id = 'mobile-block-css';

			if (style.styleSheet){
			  style.styleSheet.cssText = css;
			} else {
			  style.appendChild(document.createTextNode(css));
			}

			head.appendChild(style);
		}

		function toggleMobileBlock (event) {
			event.preventDefault();
			appendStyles();
			if(block.is(':visible')) {
				
				block.stop(true, true).fadeOut(400, () => {
					$('body').removeClass('on-mobile-open');
				});
				
			} else{
				block.stop(true, true).fadeIn(300);
				$('body').addClass('on-mobile-open');
			}
		}

		appendStyles ();

		$('.js-mbtoggle').on('click', toggleMobileBlock)
	})();

	//header bar-tip 
	(function() {
		let openedTip = 0;
		function outsideClick (e)  {
			let t = $(e.target);
			if(!t.closest('.js-bar__btn').length) {
				$('.js-bar__btn').removeClass('is-active');
				openedTip = 0;
				$(document).off('click', outsideClick)
			}
		}	

		function resizeHandler () {
			if(!openedTip) return;
				else
					$('.js-bar__btn').removeClass('is-active');
					openedTip = 0;
		}

		function handler (e){
			const self = $(this).closest('.js-bar__btn');
			const modifer = 'is-active'
			$('.js-bar__btn').not(self).removeClass(modifer);

			if(!!$(e.target).closest('.bar-tip').length) return

			if(self.hasClass(modifer)) {
				$(this).removeClass(modifer)
				openedTip = 0;
			} else {
				$(this).addClass(modifer);
				openedTip = 1;
				$(document).on('click', outsideClick)
			}
		}

		$('.js-bar__btn').on('click', handler);
		$(window).on('resize', resizeHandler);
	})();

	// page navigation dropdown
	(function() {
		let list = $('.page-nav');

		function makeDrop () {
			const div = $('<div />', {
				class: 'page-nav-drop'
			});

			div.text('Выбор раздела');
			div.on('click', pageNavToggle)

			return div
		} 

		function pageNavToggle (e) {
			e.preventDefault();
			let nav = $(this).siblings('.page-nav');
			if($(this).hasClass('is-active')) {
				
				$(nav).stop(true, true).slideUp(400, () => {
					$(this).removeClass('is-active');
					$(nav).css('display', '');
				});
			} else {

				$(nav).stop(true, true).slideDown(400, () => {
					$(this).addClass('is-active');
					$(nav).css('display', '');
				});
			}

		}

		list.each(function(idx, el) {
			const d = makeDrop ();

			if($(el).siblings('.page-nav-drop').length === 0) {
				d.insertBefore(el)
			}
		})
	})();

	// fancybox
	(function() {
		$('.fb').fancybox()
	})();

});